
.healtheducation{
		padding-left: 200px;
		font-size: .3rem;
}
.healtheducation .head{
		height: 1.7rem !important;
}
.healtheducation .head .back{
		top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.healtheducation .gray{
		color: #999999;
}
.healtheducation-head{
		border-bottom: 1px solid #eee;
    padding-left: 150px;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
    height: 1.7rem;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.healtheducation-head span{
		margin-left: 10px;
}
.healtheducation-head>div {
    display: inline-block;
    width: 33.33%;
    vertical-align: middle;
}
.head-name img{
		width: .9rem;
    height: .9rem;
    border-radius: 50%;
    margin-right: .2rem;
    vertical-align: middle;
}
.head-name>div{
		display: inline-block;
		vertical-align: middle;
}
.healtheducation-content{
		margin-top: 30px;
		padding-left: 30px;
}
.video-list,.video-item,.video-item-banner,.video-item-banner>img{
		height: 32vh;
}
.video-item{
		display: inline-block;
    width: 38%;
    position: relative;
    margin-right: 20px;
}
.video-item-banner img{
		width: 100%;
}
.video-item-icon{
		position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}
.video-item-icon img{
		height: 1.1rem;
    width: auto;
}
.video-item-text{
		position: absolute;
    bottom: 0;
    left: 0;
    color: #fff;
    background: rgba(0,0,0,0.5);
    width: 100%;
    padding: 10px 0 10px 0px;
    text-indent: 10px;
}
.article-item{
		padding: 15px 0;
    border-bottom: 1px solid #eee;
}
.article-item>div {
	    display: inline-block;
	    vertical-align: middle;
}
.article-item-left img{
		width: 1.7rem;
    height: 1.7rem;
}
.article-item-right{
    max-width: 85%;
}
.article-item-right h5{
		font-size: .3rem;
		padding-left: 15px;
}
.article-item-right p{
		margin-top: 20px;
    color: #9999;
    padding-left: 15px;
}
.QR-Code{
    position: fixed;
    top:0%;
    right:20px;
}
.QR-Code img{
		width: auto;
		height: 1.7rem;
}
	/**/
#mod_player{
		position: fixed;
    top: 0px;
    width: calc(100% - 200px);
    height: 100%;
    left: 200px;
    z-index: 10;
}
#mod_player video{
    -o-object-fit: fill;
       object-fit: fill;
}
.close_video{
		position: fixed;
    top: 20px;
    right: 10px;
    z-index: 11;
}
.win-code{
		width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.6);
    top: 0;
    left: 0;
    position: fixed;
    z-index: 10;
}
.win-code-content{
		width: 45%;
    left: 30%;
    background: #fff;
    position: absolute;
    top: 50%;
    text-align: center;
    border-radius: 10px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    padding: 20px 0;
}
.win-code-banner{
		width: 50%;
    height: auto;
}
.win-code-icon{
		position: absolute;
    top: 20px;
    right: 20px;
}
